@import url(https://fonts.googleapis.com/css?family=Montserrat:900|Raleway:400,400i,700,700i);
#root{margin:0;padding:0;background:linear-gradient(45deg, rgba(66,183,245,0.8) 0%, rgba(66,245,189,0.4) 100%);color:rgba(0,0,0,0.6);font-family:"Roboto",sans-serif;font-size:14px;line-height:1.6em;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow:scroll}.App{display:table;color:rgba(0,0,0,0.6);-webkit-font-smoothing:antialiased;padding:0;padding-top:00.5rem;border:0;font-size:100%;font:inherit;vertical-align:baseline;z-index:15;position:relative;background:#FFFFFF;width:48%;min-width:700px;min-height:400px;height:62%;border-radius:4px;box-shadow:0 0 30px rgba(0,0,0,0.1);box-sizing:border-box;margin:100px auto 10px;overflow:visible}.App::-webkit-scrollbar{display:none}

.diag,.pro,.drug{width:70%;margin:0 auto;margin-bottom:30px;text-align:center;padding-left:0%}.diag{margin-bottom:50px}.drug{text-align:left}.App-header{margin-bottom:45px;margin-top:40px}h2{display:block;margin:0 0 10px;color:rgba(0,0,0,0.6);font-size:14px;font-weight:500;line-height:1;text-transform:uppercase;letter-spacing:.2em;text-align:left}.up{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around}.down{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}#before-notification{text-align:center}

h1 {
    text-align: center;
    color: black;
    font-size: 2.3rem;
}
.outputs {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width:100%;
    margin: 30px 0px 0px 30px;
}

.outputs h3 {
    display: inline;
    margin-top: 0;
    margin-right: 10;
    color: white;
    font-size: 15px;
    padding: 0 5;
}

.outputs h3 span {
    /* background-color: rgba(66, 183, 245,0.8); */
    background-color: silver;
    box-shadow: 0.2rem;
    border-radius: 5px;
    border: 10px black;
}
  
#generate {
    color: #2f9ac4;
}

#generate:hover {
    -webkit-transform: scale(1.5);
            transform: scale(1.5); 
}

#reset:hover {
    -webkit-transform: scale(1.5);
            transform: scale(1.5); 
}

.generate-reset {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin-top: 15px;
}
*,*:before,*:after{box-sizing:border-box}html,body{margin:0;padding:0}body{background-color:#fafafa;color:#1d1f20;font-family:'Raleway', sans-serif}main{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-flex-wrap:wrap;flex-wrap:wrap}#remove{background-color:#B3C7D6FF;padding:0;margin:0px;border-radius:50%}.Item{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;margin-left:10px;margin-top:0;margin-bottom:0}.Item h4{display:inline;margin-top:0;margin-bottom:0;color:white;letter-spacing:1px}.Item h4 span{background-color:rgba(66,183,245,0.8);border-radius:2px}

